/*Product Rating  */
.hint-star {
	display: inline-block;
	margin-left: 0.5em;
	color: gold;
	width: 50%;
}

.information1 {
	max-height: 150px;
	overflow: scroll;
}

.information1::-webkit-scrollbar{
	display: none;
}

/* The most important information about the product */
#container .product-details > p {
	font-family: 'Farsan', cursive;
	text-align: center;
	font-size: 20px;
	color: #7d7d7d;
	
}

/* control1 */

.control1{
	position: relative;
	
	
}
/* the Button */
.buton {
	-webkit-transform: translateY(0px);
	        transform: translateY(0px);
	transition: 0.3s linear;
	background: #333399;
	border-radius: 5px;
  position: relative;
  overflow: hidden;
	cursor: pointer;
	outline: none;
	border: none;
	color: #eee;
	padding: 0;
	margin: 0;
	
}

.buton:hover{-webkit-transform: translateY(-4px);transform: translateY(-4px);}

.buton span {
	font-family: 'Farsan', cursive;
	transition: -webkit-transform 0.3s;
	transition: transform 0.3s;
	transition: transform 0.3s, -webkit-transform 0.3s;
	display: inline-block;
  padding: 10px 20px;
	font-size: 1.2em;
	margin:0;
	
}
/* shopping cart icon */
.buton .price, .shopping-cart{
	background: #333;
	border: 0;
	margin: 0;
}

.buton .price {
	-webkit-transform: translateX(-10%);
	        transform: translateX(-10%); padding-right: 15px;
}

/* the Icon */
.buton .shopping-cart {
	-webkit-transform: translateX(-100%);
	        transform: translateX(-100%);
  position: absolute;
	background: #333;
	z-index: 1;
  left: 0;
  top: 0;
}

/* buy */
.buton .buy {z-index: 3; font-weight: bolder;}

.buton:hover .price {-webkit-transform: translateX(-110%);transform: translateX(-110%);}

.buton:hover .shopping-cart {-webkit-transform: translateX(0%);transform: translateX(0%);}



/* product image  */
.product-image {
	transition: all 0.3s ease-out;
	display: inline-block;
	position: relative;
	overflow: hidden;
	height: 100%;
	float: right;
	width: 50%;
	display: inline-block;
}

#container img {width: 100%;height: 100%;}

.info {
    background: rgba(27, 26, 26, 0.9);
    font-family: 'Farsan', cursive;
    transition: all 0.3s ease-out;
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
    position: absolute;
    line-height: 1.9;
    text-align: left;
    font-size: 120%;
    cursor: no-drop;
    color: #FFF;
    height: 100%;
    width: 100%;
    left: 0;
    top: 0;
}

.info h2 {text-align: center}
.product-image:hover .info{-webkit-transform: translateX(0);transform: translateX(0);}

.info ul li{transition: 0.3s ease;}
.info ul li:hover{-webkit-transform: translateX(50px) scale(1.3);transform: translateX(50px) scale(1.3);}

.product-image:hover img {transition: all 0.3s ease-out;}
.product-image:hover img {-webkit-transform: scale(1.2, 1.2);transform: scale(1.2, 1.2);}

.info{
    overflow: scroll;
}

.info::-webkit-scrollbar{
    display: none;
}
body, html{
    margin:0;
    min-height:100vh  ;
    overflow: auto;
}
#root{
  height: 100%;
  min-height:100vh  ;
  min-height: 100%;
}
  .App {
    height: 100%;
    display: flex;
    background-image: url(/static/media/cloud_background.38fa550e.jpg);
    min-height:100vh  ;
    background-repeat: no-repeat;
    background-size: cover;
    color: white;
  }
  
  .appAside {
    width: 70%;
    height: 100vh;
    /* background-image: url("/e_comerce_templates/src/static/cloud_background.png"); */
  }
  
  .appForm {
    width: 30%;
    height: 100vh;

    background-color: #12130f;
    padding: 25px 40px;
    overflow: auto;
  }
  
  .pageSwitcher {
    display: flex;
    justify-content: flex-end;
    margin-bottom: 10%;
  }
  
  .pageSwitcherItem {
    background-color: #40434e;
    color: #9da6b1;
    padding: 10px 25px;
    cursor: pointer;
    font-size: 0.9em;
    border: none;
    outline: none;
    display: inline-block;
    text-decoration: none;
  }
  
  .pageSwitcherItem-active {
    background-color: #6b5b95;
    color: white;
  }
  
  .pageSwitcherItem:first-child {
    border-top-left-radius: 25px;
    border-bottom-left-radius: 25px;
  }
  .pageSwitcherItem:last-child {
    border-top-right-radius: 25px;
    border-bottom-right-radius: 25px;
  }
  
  .formCenter {
    margin-bottom: 100px;
  }
  
  .formTitle {
    color: #707c8b;
    font-weight: 300;
    margin-bottom: 50px;
  }
  
  .formTitleLink {
    color: #707c8b;
    text-decoration: none;
    display: inline-block;
    font-size: 1.7em;
    margin: 0 10px;
    padding-bottom: 5px;
  }
  
  .formTitleLink:first-child {
    margin-left: 0;
  }
  
  .formTitleLink-active {
    color: white;
    border-bottom: 1px solid #6b5b95;
  }
  
  .formField {
    margin-bottom: 40px;
  }
  
  .formFieldLabel {
    display: block;
    text-transform: uppercase;
    font-size: 0.9em;
    color: white;
  }
  .formFieldInput {
    width: 85%;
    background-color: transparent;
    border: none;
    color: white;
    outline: none;
    border-bottom: 1px solid #445366;
    font-size: 1em;
    font-weight: 300;
    padding-bottom: 10px;
    margin-top: 10px;
  }
  
  .formFieldInput::-webkit-input-placeholder {
    color: #616e7f;
  }
  
  .formFieldInput::placeholder {
    color: #616e7f;
  }
  
  .formFieldButton {
    background-color: #6b5b95;
    color: white;
    border: none;
    outline: none;
    border-radius: 25px;
    padding: 15px 70px;
    font-size: 0.8em;
    font-weight: 500;
    cursor: pointer;
  }
  
  .formFieldLink {
    color: #66707d;
    text-decoration: none;
    display: inline-block;
    border-bottom: 1.5px solid #6b5b95;
    padding-bottom: 5px;
  }
  
  .formFieldCheckboxLabel {
    color: #646f7d;
    font-size: 0.9em;
  }
  
  .formFieldCheckbox {
    position: relative;
    top: 1.5px;
  }
  
  .formFieldTermsLink {
    color: white;
    border-bottom: 1px solid #6b5b95;
    text-decoration: none;
    display: inline-block;
    padding-bottom: 2px;
    margin-left: 5px;
  }
  
  .socialMediaButtons {
    padding: 10px 100px 10px 0px;
  }
  img{
      width: 100%;
      height: 100%;
  }
